<template lang='jade'>
	.container
		el-form.topBar
			el-form-item
				el-input(type='text' v-model='searchForm.batchId' placeholder='' clearable)
			el-form-item(style='width:220px;')
				el-input(type='text' v-model='searchForm.productCode' placeholder='' clearable style='width:220px;')
			el-form-item(style='width:220px;')
				el-input(type='text' v-model='searchForm.productName' placeholder='' clearable style='width:220px;')
			el-form-item
				el-select(v-model='searchForm.status' placeholder='' clearable)
					el-option(label='' value='0')
					el-option(label='' value='1')
					el-option(label='' value='2')
			el-form-item
				el-input(type='text' v-model='searchForm.operator' placeholder='' clearable)
			el-form-item(style='width:auto')
				el-date-picker(type='datetimerange' range-separator='' start-placeholder='' end-placeholder='' format='yyyy-MM-dd HH:mm' v-model='searchTime')
			el-form-item(style='width:auto;')
				el-button(type='primary' icon='el-icon-search' size='medium' @click='searchList' v-has='"dataSearch_list_search"') 
				el-button(type='success' icon='el-icon-edit' size='medium' @click='newTask' v-has='"dataSearch_list_add"') 
			//- el-form-item(style='width:auto;margin-right:0;')
			//- 	el-button(type='success' icon='el-icon-edit' size='medium' @click='newTask') 
		.table
			el-table(:data='tableData' v-loading='loading' element-loading-text='' border @expand-change='expandRow' row-key='id')
				el-table-column(type='expand')
					template(slot-scope='props')
						//- .expandLoading(v-if='props.row.showExpandLoading')
						//- 	span ...
						//- 	//- i.el-icon-loading
						table.expendTable
							tr
								th 
								th 
								th 
								th 
								th 
							tr
								td {{props.row.statusDetail.executionProgress}}
								td {{props.row.statusDetail.successCount}}
								td {{props.row.statusDetail.failCount}}
								td {{props.row.statusDetail.updateCount}}
								td {{props.row.statusDetail.stockCount}}
				el-table-column(v-for='{prop, label, minWidth} in tableHead', :prop='prop', :label='label', :min-width='minWidth', :key='prop')
					template(slot-scope='scope')
						template(v-if='prop==="status"') {{scope.row[prop] | fixStatus}}
						template(v-else-if='prop==="prescription"') {{scope.row[prop] | fixPrescription}}
						template(v-else-if='prop==="productNameCode"')
							el-tooltip(v-if='navIsChrome' class='item' effect='dark', placement='bottom')
								div(slot='content')
									p(v-for='(code, index) in scope.row.formatProductNameCode', :key='index' style='line-height:20px;') {{code}}
								span {{scope.row[prop]}}
							div(slot='content' v-else)
								p(v-for='(code, index) in scope.row.formatProductNameCode', :key='index' style='line-height:20px;') {{code}}
						template(v-else) {{scope.row[prop]}}
				el-table-column(fixed='right' label='' min-width='120')
					template(slot-scope='scope')
						el-button(v-if='scope.row.status===0' type='text' @click='editTask(scope.row)' v-has='"dataSearch_list_edit"') 
						el-button(v-else type='text' @click='getDetail(scope.row)' v-has='"dataSearch_list_recording_search"') 
						el-button.deleteBtn(v-if='scope.row.status===0' type='text' @click='deleteTask(scope.row)' v-has='"dataSearch_list_delete"') 
			el-pagination(@size-change='handleSizeChange' @current-change='handleCurrentChange', :current-page.sync='currentPage', :page-sizes='[10, 25, 50]', :page-size='currentSize' layout='sizes, prev, pager, next, total', :total='total')
		newSearchDialog(:showNew.sync='showNewDialog' @newSuccess='updateList')
		editTaskDialog(:showEdit.sync='showEditDialog', :batchId='currentBatchId' @editSuccess='updateList')
		recordsDialog(:showRecords.sync='showRecordsDialog', :batchId='currentBatchId')
</template>
<script>
	import { getBatchRecords, getStatusDetail, deleteRecord } from '@/api/dataSearch'
	import filters from '@/filters'
	import newSearchDialog from '@/views/dialog/newSearch'
	import editTaskDialog from '@/views/dialog/editTask'
	import recordsDialog from '@/views/dialog/records'
	export default {
		filters: filters,
		components: { newSearchDialog, editTaskDialog, recordsDialog },
		data() {
			return {
				currentPage: 1,
				currentSize: 10,
				loading: false,
				total: 0,
				searchTime: ['', ''],
				searchForm: {
					batchId: '',
					status: '',
					operator: '',
					productCode: '',
					productName: '',
					startTime: '',
					endTime: '',
					pageNum: 1,
					pageSize: 10
				},
				tableHead: [
					{ prop: 'id', label: '', minWidth: '100px' },
					{ prop: 'productNameCode', label: ' | ', minWidth: '300px' },
					{ prop: 'requestParamter', label: '', minWidth: '300px' },
					{ prop: 'taskCount', label: '', minWidth: '100px' },
					{ prop: 'status', label: '', minWidth: '100px' },
					{ prop: 'prescription', label: '', minWidth: '100px' },
					{ prop: 'createUser', label: '', minWidth: '100px' },
					{ prop: 'startTime', label: '', minWidth: '150px' },
					{ prop: 'endTime', label: '', minWidth: '150px' }
				],
				tableData: [],
				showNewDialog: false,
				showEditDialog: false,
				showRecordsDialog: false,
				currentBatchId: 0,
				navIsChrome: true
			}
		},
		methods: {
			getDataList() {
				this.loading = true
				this.tableData = []
				if(!this.searchTime) {
					this.searchTime = ['', '']
				}
				this.searchForm.startTime = filters.fixTimeStamp(new Date(this.searchTime[0]).getTime()) + (this.searchTime[0] ? ':00' : '')
				this.searchForm.endTime = filters.fixTimeStamp(new Date(this.searchTime[1]).getTime()) + (this.searchTime[1] ? ':00' : '')
				this.searchForm.pageNum = this.currentPage
				this.searchForm.pageSize = this.currentSize
				getBatchRecords(this.searchForm)
					.then((res) => {
						this.loading = false
						this.total = res.data.total
						this.tableData = res.data.list
						for (var item of this.tableData) {
							item.formatProductNameCode = item.productNameCode.split(';')
							item.statusDetail = ''
							item.showExpandLoading = false
						}
					})
					.catch(() => {
						this.loading = false
						console.log('getBatchRecords failed')
					})
			},
			expandRow(row, expandedRows) {
				this.tableData.map((item, index, arr) => {
					if (item.id === row.id) {
						this.tableData[index].showExpandLoading = true
					}
				})
				getStatusDetail({ batchId: row.id })
					.then((res) => {
						this.tableData.map((item, index, arr) => {
							if (this.tableData[index].id === row.id) {
								this.tableData[index].statusDetail = res.data
								this.tableData[index].showExpandLoading = false
								this.$set(this.tableData, index, this.tableData[index])
							}
						})
					})
					.catch(function (error) {
						console.log(error)
					})
			},
			searchList() {
				this.currentPage = 1
				this.getDataList()
			},
			handleSizeChange(currentSize) {
				this.currentSize = currentSize
				this.getDataList()
			},
			handleCurrentChange(currentPage) {
				this.currentPage = currentPage
				this.getDataList()
			},
			newTask() {
				this.showNewDialog = true
			},
			editTask(row) {
				this.currentBatchId = row.id
				this.showEditDialog = true
			},
			getDetail(row) {
				this.currentBatchId = row.id
				this.showRecordsDialog = true
			},
			deleteTask(row) {
				this.$confirm(` ${row.id} `, '', {
					confirmButtonText: '',
					cancelButtonText: '',
					cancelButtonClass: 'cancelBtn',
					type: 'warning'
				}).then(() => {
					var params = {
						batchId: row.id,
						operator: localStorage.getItem('u') && JSON.parse(localStorage.getItem('u')).username
					}
					deleteRecord(params)
						.then((res) => {
							this.$message({
								type: 'success',
								message: '!'
							})
							this.updateList()
						})
						.catch((error) => {
							console.log(error)
							this.$message.error('')
						})
				}).catch(() => {
					// this.$message({
					//	 type: 'info',
					//	 message: ''
					// })		
				})
			},
			updateList() {
				this.showEditDialog = false
				this.showNewDialog = false
				this.getDataList()
			}
		},
		created() {
			this.getDataList()
			this.navIsChrome = navigator.userAgent.includes("Chrome")
		}
	}
</script>
<style lang='stylus' scoped>
.container
	.el-form.topBar
		background #fff
		padding 20px 15px 5px 15px
		box-shadow 1px 1px 3px #dedede
		.el-form-item
			display inline-block
			width 180px
			margin-right 10px
			margin-bottom 15px
			// &:nth-child(2n + 1)
			// 	width 120px
			// 	.el-input
			// 		width 120px
			.el-input
				width 180px
			>>>.el-input__inner
				padding 0 10px
				&:focus
					border-color #67C23A
			>>>.el-date-editor
				width 335px
				vertical-align bottom
				.el-range-separator,
				.el-icon-time,
				.el-range__close-icon
					line-height 40px
					width 25px
			.el-button--medium
				padding 11px 10px
			>>>.el-select
				width 180px
				.el-input
					width 180px
	.table
		padding 20px 15px 1px 15px
		background #fff
		margin-top 20px
		box-shadow -1px -1px 3px #eee
		.el-table
			font-size 13px
			>>>td,
			>>>th
				color #222
				padding 8px 0
			>>>td:nth-child(3)
				.cell
					overflow hidden
					text-overflow ellipsis
					white-space nowrap
					-webkit-line-clamp 1
					-webkit-box-orient vertical
					word-break break-all
					width 100%
			>>>.el-tooltip
				text-overflow ellipsis
				width 100%
				display inline-block
				overflow hidden
			>>>.el-table__expanded-cell
				background #f0f2f5
				height 87px
				.expandLoading
					position relative
					top 0
					left 50px
					.el-icon-loading
						transform translate(0, 50%)
						font-size 20px
				.expendTable
					margin 5px 50px
					th
						background #eee
						text-align center
						color #666
					td
						width 180px
						text-align center
						&:hover
							background #fff
		.el-button
			font-weight 600
		.el-button--warning
			background #FF8A00
			border-color #FF8A00
			&:hover
				opacity 0.8
		.el-button--success.is-disabled,
		.el-button--warning.is-disabled
			opacity 0.4
		.deleteBtn
			color #F56C6C
	>>>.el-dialog
		margin-top 6vh !important
		.el-dialog__body
			padding 5px 20px
			.el-form-item
				margin-bottom 30px
				&:last-child
					margin-bottom 0
			.el-form-item__label
				padding 0
				line-height 30px
			.el-form-item__content
				line-height 30px
			table
				border 1px solid #DCDFE6
				text-align left
				tr
					th
						background #f2f2f2
						padding 0 10px
						color #666
						border-bottom 1px solid #DCDFE6
						&:first-child
							border-right 1px solid #DCDFE6
					td
						width auto
						padding 10px
						line-height 20px
						&:first-child
							border-right 1px solid #DCDFE6
		.el-select
			width 100%
			.el-input
				width 100%
		.fileName
			display inline-block
			width calc(100% - 90px)
			margin-left 10px
>>>.cancelBtn
	background red
</style>


